import React from "react"
import "./style.css"
import Text from "../text/text"
import translate from "../../translate"

const Locations = ({locations}) => (
    <div className={"locations"}>
        <img src={require('./../../images/map.png')} alt={"Deutschlandkarte mit Standorten"}/>
        <div className={"locationswrapper"}>


            {locations.map(location => {
                return <div className={"location"}>
                    <h3>{location.name}</h3>
                    <br/>
                    <Text>{location.description}</Text>
                    <br/>
                    <Text>{require('./../../' + location.person + ".json").phone}</Text>
                    <Text>{require('./../../' + location.person + ".json").street}</Text>
                    <Text>{require('./../../' + location.person + ".json").plz} {require('./../../' + location.person + ".json").place}</Text>
                </div>;
            })}

            <div>

            </div>
        </div>
    </div>
);

export default Locations
