import React from "react"
import "./style.css"
import ShiftGrid from "../../elements/shiftgrid"
import Voice from "../../elements/voice/voice"
import {i18n} from "../../i18n/language";


const VoicesComponent = ({persons}) => {
  return <ShiftGrid>
    {persons.map(person => {
      return <Voice id={person.name.toLowerCase()} person={person} style={{ "width": "260px" }}/>
    })}
  </ShiftGrid>
}

export default VoicesComponent
