import React from "react"
import "./style.css"
import Person from "../person/contactblock"
import Audio from "../audio/audio"

const Voice = ({ id, person, children }) => (
  <Person hasAudio={true} onClick={() => {
    var myAudio = document.getElementById(id)
    if (myAudio.paused) {
      myAudio.play()
    } else {
      myAudio.pause()
    }
  }} person={{ image: id + ".jpg", name: id, id: id, ...person }} className={"voice"}>
    {children || <Audio id={id}>{id + ".mp3"}</Audio>}
    {children && <Audio style={{ "display": "none" }} id={id}>{id + ".mp3"}</Audio>}
  </Person>
)

export default Voice
